import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Div, View, Group, Form, Card, Root,InfoRow,List,Cell,Link,PullToRefresh} from '@vkontakte/vkui';
import persik from '../img/persik.png';
import './Persik.css';
import ReactDOM from 'react-dom'
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

export default class Persik extends React.Component {

	constructor(props){
	super(props);

	this.state={
		events:[],
		activeView:"home",
		title:"",
		text:"",
		mesto:"",
		stattime:"",
		owner_id:"",
		chatlink:"",
		type:"",
		fetching: false,
		}
	}

	get(url){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.send();
		return xhr.responseText;
	}

	componentWillMount() {
		this.load()
	}
	reload(){
		this.setState({events:[]})
		this.load()
	}
	load(){
		this.setState({fetching:true})

		if(this.state.events.length==0){
			var exp=""
			var re=this.get("https://api.finebot.site/events/events.get")
			re=JSON.parse(re)
			var i =0
			for(i;i<re.response.length;i++){

				var mesto=re.response[i].country+", "+re.response[i].city+", "+re.response[i].street
				this.state.events.push(encodeURIComponent(re.response[i].title)+"&&&"+encodeURIComponent(mesto)+"&&&"+encodeURIComponent(re.response[i].starttime)+"&&&"+encodeURIComponent(re.response[i].chatlink)+"&&&"+encodeURIComponent(re.response[i].owner_id)+"&&&"+encodeURIComponent(re.response[i].text)+"&&&"+encodeURIComponent(re.response[i].type))


			}
		}
		this.setState({fetching:false})
		//document.getElementById("test").
	}
	load1(){
		var exp=""
		var re=this.get("https://api.finebot.site/events/events.get")
		re=JSON.parse(re)
		var i =0
		for(i;i<re.response.length;i++){
			ReactDOM.render(<Group><Div style={{textAlign:'down'}}>Title</Div><Div style={{fontSize:12, textAlign:'top'}}>Text<br/>tes</Div></Group>,document.getElementById("test"))
		}

	};
	goo(i){
		var title=decodeURIComponent(this.state.events[i].split("&&&")[0])
		var mesto=decodeURIComponent(this.state.events[i].split("&&&")[1])
		var datetime=decodeURIComponent(this.state.events[i].split("&&&")[2])
		var chatlink=decodeURIComponent(this.state.events[i].split("&&&")[3])
		var owner_id=decodeURIComponent(this.state.events[i].split("&&&")[4])
		var text=decodeURIComponent(this.state.events[i].split("&&&")[5])
		var type=decodeURIComponent(this.state.events[i].split("&&&")[6])
		this.setState({activeView:"full",title:title,text:text,mesto:mesto,starttime:datetime,owner_id:owner_id,chatlink:chatlink,type:type});
	}
	ret(item, i){

		var title=decodeURIComponent(item.split("&&&")[0])
		var mesto=decodeURIComponent(item.split("&&&")[1])
		var datetime=decodeURIComponent(item.split("&&&")[2])

		return(
			<Group onClick={()=>{this.goo(i)}}><Div style={{textAlign:'down'}}>{title}</Div><Div style={{fontSize:12, textAlign:'top'}}>{decodeURIComponent(mesto).split("+").join(" ")}<br/>{datetime}</Div></Group>
		);
	}
	getOwner(){
		var own="https://vk.com/id"+this.state.owner_id.toString();
		if(this.state.owner_id.toString()==="undefined"){
			return ""
		}else{
			return (<Cell>
				<InfoRow title="Организатор">
					<Link href={own}>vk.com/id{this.state.owner_id}</Link>
				</InfoRow>
			</Cell>);
		}
	}
	render() {

		const props=this.props;

		return (
		<Root activeView={this.state.activeView}>

			<View id="home">
				<PullToRefresh onRefresh={()=>{this.reload()}} isFetching={this.state.fetching}>
				<View>
						{this.state.events.map(this.ret.bind(this))}
				</View>
				</PullToRefresh>
			</View>
			<View id="full">
				<Group><Div>{this.state.title}</Div></Group>
				<Group>
					<Div>
						<InfoRow title="Примечание">
							{this.state.text}
						</InfoRow>
					</Div>
				</Group>
				<Group>
					<List>
						<Cell>
							<InfoRow title="Место">
								{decodeURIComponent(this.state.mesto)}
							</InfoRow>
						</Cell>
						<Cell>
							<InfoRow title="Начало">
								{decodeURIComponent(this.state.starttime)}
							</InfoRow>
						</Cell>
						<Cell>
							<InfoRow title="Тип">
								{decodeURIComponent(this.state.type)}
							</InfoRow>
						</Cell>
						{this.getOwner()}
					</List>
				</Group>
			</View>
		</Root>
		);
	}
}
const style={

};
Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};


